import { DraggableDataProps, DragHanderProps } from '../types';

export default function updateDragHander(draggableData: DraggableDataProps, payload?: DragHanderProps) {
  const { dragHander, ...rest } = draggableData;
  // 避免频繁更新
  if (!payload && !dragHander) return draggableData;

  if (!payload) {
    return rest;
  }

  if (payload.viewKey === dragHander?.viewKey) return draggableData;

  return {
    ...rest,
    dragHander: payload,
  };
}
